.site-navigation{
  font-size: $font-size-m;
  font-weight:$font-weight-bold;
  line-height: $line-height-s;
  display: flex;
  flex-direction: column;
}

.site-navigation__first-block{

}

.site-navigation__second-block{

}

.site-navigation__logo{

}

.site-navigation__logo-image{

}

.site-navigation__toggle{

}

.site-navigation__nav{

}

.site-navigation__item{
  color: $main-color;
}

.site-navigation__link{
  color: $main-color;
}

.site-navigation__input{
  font:inherit;
}


@media (min-width: $tablet-full-width){
  .site-navigation{
    flex-direction: row;
  }

  .site-navigation__first-block{
    display: flex;
    flex-wrap: wrap;
  }

  .site-navigation__second-block{
    display: flex;
  }
}

@media (min-width: $desktop-full-width){
  .site-navigation__logo{
    order: 2;
  }
}
